<template>
    <div class="container">
            <div class="wrap">
                <div class="sideBar">
                    <i @click="isDisplay = !isDisplay" class="fa fa-list-ul menuBtn hidden-sm-and-up"></i>
                    <el-menu :class="{personMenu:true,isDisplay:isDisplay}" router :default-active="this.$route.path.replace(/.*\/([^\/]+)$/, '$1')" :collapse="isCollapse" background-color="#5eadc7" text-color="#fff" active-text-color="#ffd04b">
                        <i @click="isCollapse = !isCollapse" class="fa fa-list-ul"></i>
                        <div class="personMsg">
                            <img :src="this.$store.state.user.img">
                            <div>
                                <p>{{ this.$store.state.user.name }}</p>
                            </div>
                        </div>
                        <el-menu-item index="myMessage">
                            <i class="el-icon-edit-outline"></i>
                            <span slot="title">个人资料</span>
                        </el-menu-item>
                        <el-menu-item index="myActivity">
                            <i class="el-icon-menu"></i>
                            <span slot="title">动态管理</span>
                        </el-menu-item>
                        <el-menu-item index="myProject">
                            <i class="el-icon-document"></i>
                            <span slot="title">项目信息</span>
                        </el-menu-item>
                        <el-menu-item index="myNews">
                            <i class="el-icon-setting"></i>
                            <span slot="title">我的消息</span>
                        </el-menu-item>
                        <el-menu-item index="mySetting">
                            <i class="el-icon-setting"></i>
                            <span slot="title">我的设置</span>
                        </el-menu-item>
                    </el-menu>
                </div>
                <el-col class="allContent" :span="22">
                    <router-view></router-view>
                </el-col>
            </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            isCollapse:true,
            isDisplay:true
        }
    },
    methods:{

    }
}
</script>
<style lang="scss" scoped>
.container {
    background: rgb(237, 241, 243);
    color: white;
    .wrap {
        width: 70%;
        max-width:1250px;
        margin: 100px auto;
        background: #ffffff;
        // padding: 30px 0;
        position: relative;
        overflow:hidden;
        padding-left: 200px;
        min-height: 800px;
        @media screen and(max-width: 1150px) {
            padding-left: 100px;
        }
        @media screen and(max-width: 1000px) {
            width:auto;
            margin-left: 10px;
            margin-right: 10px;
        }
        @media screen and(max-width: 375px) {
            padding-left: 0;
            margin-left: 0;
            margin-right: 0;
        }
        .isDisplay{
            display:none;
        }
        .sideBar {
            overflow: hidden;
            transition: all 1s ease-out;
            width: 200px;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 999;
            .menuBtn{
                display: block;
                padding:10px;
                border-radius: 50%;
                color:rgb(121, 118, 118);
                border: 2px solid rgb(175, 195, 221);
                position:fixed;
                bottom: 120px;
                right:20px;
                z-index: 1999;
            }
            .personMenu {
                box-sizing: border-box;
                padding-top: 20px;
                height: 100%;
                .fa-list-ul{
                    margin-left: 20px;
                    cursor:pointer;
                }
                .el-menu-vertical-demo:not(.el-menu--collapse) {
                    width: 200px;
                    min-height: 400px;
                }
                .el-menu-item i {
                    color: #fff;
                }
                /deep/ .el-radio-button__inner {
                    background: none;
                    border: none;
                    color: white;
                }
                .personMsg {
                    // padding: 20px;
                    text-align: center;
                    img {
                        width: 50%;
                        border-radius: 50%;
                        border: 2px solid #ffffff;
                        margin-top: 20px;
                        background: rgb(238, 234, 234);
                    }
                    p{
                        overflow: hidden;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                }
            }
        }
        .allContent {
            padding-top: 10px;
        }
    }
}
</style>



